\n  gap: 20px;\n  flex-direction: column;\n  align-items: center;\n  text-align: center;\n}\n\n.jumbotron-content h1 {\n  font-family: \"Raleway\";\n  font-size: 4.4vmax;\n  font-weight: 700;\n  color: rgb(255, 255, 255);\n}\n\n.jumbotron-content p {\n  font-size: 1.5vmax;\n  font-weight: 400;\n}\n\n.jumbotron-content a {\n  display: block;\n  text-decoration: none;\n  padding: 12px 22px;\n  background-color: transparent;\n  border: 2px solid white;\n  border-radius: 3.5px;\n  color: white;\n  font-family: 'Raleway';\n  font-size: .9rem;\n  font-weight: 600;\n  letter-spacing: -.5px;\n  margin-top: 10px;\n  transition: all .3s ease;\n  cursor: pointer;\n}\n\n.jumbotron-content a:hover {\n  background-color: white;\n  color: var(--black);\n}\n\n.container {\n  max-width: 1200px;\n  padding-inline: 30px;\n  width: 100%;\n}\n\nsvg {\n  color: var(--topping);\n}\n\n.banner {\n  width: 100%;\n  padding-block: 50px;\n  display: grid;\n  column-gap: 40px;\n  grid-template-columns: repeat(3, 1fr);\n  border-bottom: 1px solid rgba(113, 145, 146, .35);\n}\n\n.banner-card {\n  display: flex;\n  gap: 5px;\n  flex-wrap: nowrap;\n  flex-direction: column;\n  align-items: center;\n  text-align: center;\n  color: var(--topping);\n}\n\n.banner-card i {\n  font-size: 4.5rem;\n  margin-bottom: 10px;\n}\n\n.banner-card h4 {\n  /* font-family: \"Raleway\"; */\n  font-family: \"Lato\";\n  font-size: 1.10rem;\n  font-weight: 600;\n}\n\n.banner-card p {\n  font-size: 1rem;\n  font-weight: 300;\n}\n\n#mainContent {\n  max-width: 1200px;\n  width: 100%;\n  padding-block: 50px;\n  display: flex;\n  gap: 80px;\n  flex-direction: column;\n  align-items: center;\n}\n\n.heading {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  color: var(--black);\n  gap: 20px;\n  text-align: center;\n}\n\n.heading h2 {\n  font-family: \"Raleway\";\n  font-size: 2.5rem;\n}\n\n.heading p {\n  font-size: 1.10rem;\n}\n\n.heading h2 span {\n  color: var(--topping);\n}\n\n.search-area {\n  margin-top: 20px;\n  display: flex;\n  background-color: pink;\n  align-items: center;\n}\n\n.search-bar {\n  width: 250px;\n  height: 50px;\n  font-size: 1rem;\n  border: 3px solid var(--black);\n  border-radius: 3px;\n\n  position: relative;\n  z-index: 1;\n}\n\n.search-bar i {\n  background-color: white;\n  padding-inline: 5px;\n  font-size: 1.65rem;\n\n  position: absolute;\n  top: -12px;\n  left: 36px;\n\n  transition: .3s ease-out;\n}\n\n.search-bar input {\n  width: 100%;\n  height: 100%;\n  padding-left: 80px;\n  border: none;\n  outline: none;\n  background-color: white;\n  font-family: \"Lato\";\n  font-size: 1rem;\n  font-weight: 500;\n  color: var(--black);\n\n  transition: .3s ease-out;\n}\n\n.search-area button {\n  height: 50px;\n  padding: 16px 6px;\n  background-color: var(--black);\n  border: 3px solid var(--black);\n  border-radius: 2px;\n  font-size: .8rem;\n  color: white;\n  outline: none;\n  font-weight: bolder;\n\n  margin-left: -50px;\n  transition: .3s ease-in;\n  cursor: pointer;\n}\n\n.search-bar:hover i {\n  position: absolute;\n  top: -12px;\n  left: -15px;\n}\n\n.search-area:hover button {\n  margin-left: -3px;\n}\n\n.posts {\n  display: grid;\n  grid-template-columns: repeat(3, 1fr);\n  gap: 0px 30px;\n}\n\n.post {\n  min-height: 350px;\n  height: 100%;\n  /* height: 350px; */\n  font-family: \"Raleway\";\n  /* cursor: pointer; */\n  text-decoration: none;\n  color: inherit;\n  transition: .3s ease;\n  display: flex;\n  gap: 15px;\n  flex-direction: column;\n  align-items: start;\n}\n\n.post .post-image {\n  width: 100%;\n  background-color: rgb(255, 255, 255);\n  /* height: fit-content; */\n  height: 200px;\n  overflow: hidden;\n}\n\n.post img {\n  width: 100%;\n  /* max-height: 250px; */\n  object-fit: cover;\n  object-position: center;\n  transition: .8s ease-in;\n}\n\n.post p {\n  text-transform: uppercase;\n  font-size: 1rem;\n  letter-spacing: 3px;\n}\n\n.post h3 {\n  font-size: 1.85rem;\n  font-weight: 700;\n  margin-left: -2.5px;\n  padding-left: 0;\n}\n\n.post img:hover {\n  transform: scale(1.12);\n}\n\n.post:hover {\n  color: var(--topping);\n  color: #87bbbd;\n}\n\nfooter {\n  width: 100%;\n  padding: 30px;\n  background-color: var(--black);\n  color: white;\n  display: flex;\n  flex-direction: column;\n  align-items: start;\n  gap: 20px;\n}\n\nfooter ul {\n  list-style-type: none;\n  display: flex;\n  gap: 50px;\n  align-items: center;\n}\n\nfooter a {\n  color: white;\n  font-size: 1rem;\n  font-weight: 300;\n  display: block;\n  height: 44px;\n}\n\nfooter p {\n  font-size: 1.2rem;\n  font-weight: 600;\n}\n\n.skip-button {\n  display: block;\n  padding: 10px 20px;\n  background-color: var(--topping);\n  color: white;\n  text-decoration: none;\n  border-right: 2px solid white;\n  border-bottom: 2px solid white;\n  border-bottom-right-radius: 10px;\n\n  position: fixed;\n  z-index: 99;\n  top: -100%;\n  left: 0;\n  transition: .3s ease;\n}\n\n.skip-button:focus {\n  top: 0;\n}\n\n#stack-menu {\n  display: none;\n}\n\nh2.placeholder {\n  font-weight: bold;\n  color: var(--black);\n  text-align: center;\n}\n\n/* DETAIL PAGE */\n\n\n.pattern {\n  width: 100%;\n  height: 80px;\n  background-color: #f6f6f6;\n  /* background-image: url(\"../public/images/pattern/daun.webp\"); */\n  filter: brightness(50%);\n}\n\n.container-detail {\n  max-width: 1200px;\n  padding-inline: 30px;\n  width: 100%;\n  /*\n  for detail page\n   */ \n  margin-top: 40px;\n  background: rgb(217,217,217);\n  background: linear-gradient(90deg, rgba(217,217,217,1) 59%, rgba(246,246,246,1) 77%);\n  display: grid;\n  grid-template-columns: 1fr 1fr;\n}\n\n.detail-image img {\n  max-width: 600px;\n  width: 100%;\n}\n\n.detail-text {\n  padding-inline-start: 40px;\n  font-family: 'Raleway', sans-serif;\n  font-size: 18px;\n  display: flex;\n  flex-direction: column;\n  align-items: start;\n  gap: 30px;\n}\n\n.detail-text h1 {\n  font-size: calc(2 * 1em + 2px);\n  font-weight: 500;\n}\n\n.detail-text h4 {\n  font-family: 'Lato', sans-serif;\n  font-size: .7em;\n  font-weight: 300;\n  margin-top: -28px;\n}\n\n.detail-text  p{\n  line-height: 30px;\n}\n\n.detail-text span {\n  text-decoration: underline;\n}\n\n.detail-text div.menu-container {\n  margin-left: 20px;\n  margin-top: -12px;\n  display: flex;\n  justify-content: center;\n  align-items: left;\n  gap: 40px;\n}\n\n.detail-text ul {\n  list-style-type: circle;\n}\n\n.detail-text li {\n  margin-bottom: 10px;\n}\n/* \n.detail-text button {\n  background-color: transparent;\n  border-radius: 2px;\n  border: 2px solid rgb(57, 57, 57);\n  font-family: 'Lato';\n  font-weight: 700;\n  padding: 12px 20px;\n  letter-spacing: 1.2px;\n  transition: .3s;\n}\n\n.detail-text button:hover {\n  background-color: rgb(54, 54, 54);\n  color: white;\n}\n\n.detail-text button:focus {\n  outline: 4px double var(--topping);\n} */\n\n\n.detail-text button {\n  border: none;\n  background-color: transparent;\n  padding: 6px;\n  cursor: pointer;\n}\n\n.detail-text button img {\n  width: 32px;\n}\n\n/* TESTIMONI */\n\n.testimoni {\n  width: 100%;\n  margin-block-start: 80px;\n  margin-block-end: 5px;\n  min-height: 250px;\n  height: 100%;\n  background-color: rgb(0, 0, 0);\n  background-position: center;\n  color: white;\n  font-family: \"Raleway\";\n  overflow: hidden;\n}\n\n.testimoni-overlay {\n  width: 100%;\n  min-height: 250px;\n  height: 100%;\n  background-color: rgba(0, 0, 0, .65);\n  display: flex;\n  justify-content: space-around;\n}\n\n.testimoni-title h2{\n  margin-top: 40px;\n  font-size: 42px;\n  font-weight: 600;\n}\n\n.testimoni-title span {\n  display: block;\n  margin-top: 3.5px;\n  margin-left: 5px;\n  width: 50%; \n  height: 4px;\n  background-color: white;\n}\n\n.testimoni-card {\n  max-width: 280px;\n  width: 100%;\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  align-items: center;\n  gap: 30px;\n}\n\n.testimoni-card p {\n  text-align: center;\n}\n\n\n.testimoni-card span {\n  font-weight: 500;\n  /* font-family: \"Lato\"; */\n}\n\n.empty-sauce {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  gap: 10px;\n}\n\n.empty-sauce img {\n  width: 200px;\n}\n\n.empty-sauce img + p {\n  font-size: 24px;\n  font-weight: 500;\n}\n\n.empty-sauce p {\n  font-size: 14px;\n  font-weight: 300;\n}\n\n.loader-container {\n  position: fixed;\n  z-index: 100;\n  top: 0;\n  bottom: 0;\n  left: 0;\n  right: 0;\n  /* background-color: rgba(241, 242, 243, .5); */\n  background-color: rgba(68, 68, 68, 0.8);\n  display: flex;\n}\n\n.loader-container img {\n  margin: auto;\n  width: 80px;\n}\n\n", "",{"version":3,"sources":["webpack://./src/styles/style.css"],"names":[],"mappings":"AAEA;EACE,eAAe;EACf,gBAAgB;EAChB,kBAAkB;AACpB;;AAEA;EACE,SAAS;EACT,UAAU;EACV,sBAAsB;AACxB;;AAEA;EACE,cAAc;AAChB;;AAEA;EACE,+BAA+B;EAC/B,uBAAuB;EACvB,mBAAmB;EACnB,aAAa;EACb,sBAAsB;EACtB,mBAAmB;AACrB;;AAEA;EACE,eAAe;EACf,MAAM;EACN,OAAO;EACP,QAAQ;EACR,WAAW;EACX,yCAAyC;EACzC,aAAa;EACb,uBAAuB;EACvB,mBAAmB;EACnB,oBAAoB;EACpB,wBAAwB;AAC1B;;AAEA;EACE,YAAY;EACZ,iBAAiB;EACjB,WAAW;EACX,aAAa;EACb,8BAA8B;EAC9B,mBAAmB;AACrB;;AAEA;EACE,qBAAqB;EACrB,aAAa;AACf;;AAEA;EACE,iBAAiB;EACjB,2DAA2D;EAC3D,YAAY;AACd;;AAEA;EACE,kBAAkB;EAClB,gCAAgC;EAChC,YAAY;EACZ,qBAAqB;EACrB,+BAA+B;EAC/B,gBAAgB;EAChB,qBAAqB;EACrB,oBAAoB;AACtB;;AAEA;EACE,oCAAoC;AACtC;;AAEA;EACE,WAAW;EACX,aAAa;EACb,sBAAsB;EACtB,mBAAmB;AACrB;;AAEA;EACE,WAAW;EACX,aAAa;EACb,uBAAuB;AACzB;;AAEA;EACE,iBAAiB;EACjB,WAAW;EACX,YAAY;EACZ,cAAc;EACd,kBAAkB;EAClB,gBAAgB;AAClB;;AAEA;EACE,WAAW;EACX,YAAY;EACZ,cAAc;EACd,YAAY;EACZ,iBAAiB;EACjB,uBAAuB;AACzB;;AAEA;EACE,kBAAkB;EAClB,MAAM;EACN,SAAS;EACT,OAAO;EACP,QAAQ;EACR,aAAa;EACb,mCAAmC;AACrC;;AAEA;EACE,YAAY;EACZ,cAAc;EACd,YAAY;EACZ,aAAa;EACb,SAAS;EACT,sBAAsB;EACtB,mBAAmB;EACnB,kBAAkB;AACpB;;AAEA;EACE,sBAAsB;EACtB,kBAAkB;EAClB,gBAAgB;EAChB,yBAAyB;AAC3B;;AAEA;EACE,kBAAkB;EAClB,gBAAgB;AAClB;;AAEA;EACE,cAAc;EACd,qBAAqB;EACrB,kBAAkB;EAClB,6BAA6B;EAC7B,uBAAuB;EACvB,oBAAoB;EACpB,YAAY;EACZ,sBAAsB;EACtB,gBAAgB;EAChB,gBAAgB;EAChB,qBAAq