0;400;500;600;700;800&display=swap');\n\n:root {\n  font-size: 16px;\n  --black: #3C4245;\n  --topping: #719192;\n}\n\n* {\n  margin: 0;\n  padding: 0;\n  box-sizing: border-box;\n}\n\nimg {\n  display: block;\n}\n\nbody {\n  font-family: 'Lato', sans-serif;\n  background-color: white;\n  color: var(--black);\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n}\n\nheader {\n  position: fixed;\n  top: 0;\n  left: 0;\n  right: 0;\n  z-index: 10;\n  /* border-bottom: 1px solid aquamarine; */\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  padding-inline: 30px;\n  transition: all .3s ease;\n}\n\nheader nav {\n  height: 80px;\n  max-width: 1200px;\n  width: 100%;\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n}\n\nheader ul {\n  list-style-type: none;\n  display: flex;\n}\n\nheader .logo {\n  margin-left: 30px;\n  /* + padding kiri header 30px  |  nysesuaikan dg padding a*/\n  width: 100px;\n}\n\nheader li a {\n  padding: 30px 40px;\n  /* + padding kanan header 30px */\n  color: white;\n  text-decoration: none;\n  font-family: \"Lato\", sans-serif;\n  font-weight: 900;\n  letter-spacing: 1.5px;\n  transition: .3s ease;\n}\n\nheader li a:hover {\n  background-color: rgba(0, 0, 0, 0.2);\n}\n\nmain {\n  width: 100%;\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n}\n\ndiv.jumbotron-container {\n  width: 100%;\n  height: 100vh;\n  background-color: black;\n}\n\ndiv.jumbotron {\n  max-width: 1300px;\n  width: 100%;\n  height: 100%;\n  margin: 0 auto;\n  position: relative;\n  overflow: hidden;\n}\n\ndiv.jumbotron img {\n  width: 100%;\n  height: 100%;\n  display: block;\n  margin: auto;\n  object-fit: cover;\n  object-position: center;\n}\n\ndiv.overlay {\n  position: absolute;\n  top: 0;\n  bottom: 0;\n  left: 0;\n  right: 0;\n  display: flex;\n  background-color: rgba(0, 0, 0, .3);\n}\n\n.jumbotron-content {\n  color: white;\n  max-width: 50%;\n  margin: auto;\n  display: flex;\n  gap: 20px;\n  flex-direction: column;\n  align-items: center;\n  text-align: center;\n}\n\n.jumbotron-content h1 {\n  font-family: \"Raleway\";\n  font-size: 4.4vmax;\n  font-weight: 700;\n  color: rgb(255, 255, 255);\n}\n\n.jumbotron-content p {\n  font-size: 1.5vmax;\n  font-weight: 400;\n}\n\n.jumbotron-content a {\n  display: block;\n  text-decoration: none;\n  padding: 12px 22px;\n  background-color: transparent;\n  border: 2px solid white;\n  border-radius: 3.5px;\n  color: white;\n  font-family: 'Raleway';\n  font-size: .9rem;\n  font-weight: 600;\n  letter-spacing: -.5px;\n  margin-top: 10px;\n  transition: all .3s ease;\n  cursor: pointer;\n}\n\n.jumbotron-content a:hover {\n  background-color: white;\n  color: var(--black);\n}\n\n.container {\n  max-width: 1200px;\n  padding-inline: 30px;\n  width: 100%;\n}\n\nsvg {\n  color: var(--topping);\n}\n\n.banner {\n  width: 100%;\n  padding-block: 50px;\n  display: grid;\n  column-gap: 40px;\n  grid-template-columns: repeat(3, 1fr);\n  border-bottom: 1px solid rgba(113, 145, 146, .35);\n}\n\n.banner-card {\n  display: flex;\n  gap: 5px;\n  flex-wrap: nowrap;\n  flex-direction: column;\n  align-items: center;\n  text-align: center;\n  color: var(--topping);\n}\n\n.banner-card i {\n  font-size: 4.5rem;\n  margin-bottom: 10px;\n}\n\n.banner-card h4 {\n  /* font-family: \"Raleway\"; */\n  font-family: \"Lato\";\n  font-size: 1.10rem;\n  font-weight: 600;\n}\n\n.banner-card p {\n  font-size: 1rem;\n  font-weight: 300;\n}\n\n#mainContent {\n  max-width: 1200px;\n  width: 100%;\n  padding-block: 50px;\n  display: flex;\n  gap: 80px;\n  flex-direction: column;\n  align-items: center;\n}\n\n.heading {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  color: var(--black);\n  gap: 20px;\n  text-align: center;\n}\n\n.heading h2 {\n  font-family: \"Raleway\";\n  font-size: 2.5rem;\n}\n\n.heading p {\n  font-size: 1.10rem;\n}\n\n.heading h2 span {\n  color: var(--topping);\n}\n\n.search-area {\n  margi